@import url(https://fonts.googleapis.com/css?family=Roboto:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

hr {
  width: 50%;
}

.App {
  text-align: center;
}

.inline-block {
  display: inline-block;
  height: 3em;
  width: 3em;
  margin: 10px;
}

.content {
  float: right;
  width: 70%;
  padding-right: 30px;
}

.logo {
  pointer-events: none;
  height: 100%;
  width: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .logo {
    -webkit-animation: 1s linear infinite alternate slidein;
            animation: 1s linear infinite alternate slidein;

  }
}

@-webkit-keyframes slidein {
  from {
    transform: scaleX(.6);
  }

  to {
    transform: scaleX(1);
  }
}

@keyframes slidein {
  from {
    transform: scaleX(.6);
  }

  to {
    transform: scaleX(1);
  }
}

@media only screen and (max-width: 600px) {
  .content {
    float: none;
    width: 100%;
    padding: 20px;
  }
}
.brand-statement {
    margin-top: 50px;
}
.bottom-links {
    margin-bottom: 20px;
    width: 100%;
}

.bottom-link {
    display: inline-block;
    margin: 10px;
    width: 35px;
}
@-webkit-keyframes showTopText {
  0% {
    transform: translate3d(0, 100%, 0);
  }

  /* 40%, 60% { transform: translate3d(0, 50%, 0); } */
  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes showTopText {
  0% {
    transform: translate3d(0, 100%, 0);
  }

  /* 40%, 60% { transform: translate3d(0, 50%, 0); } */
  100% {
    transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes showBottomText {
  0% {
    transform: translate3d(0, -100%, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes showBottomText {
  0% {
    transform: translate3d(0, -100%, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

.sidebar {
  float: left;
  width: 22%;
  height: 100vh;
  position: fixed;
}

.animated-title {
  color: #222;
  font-family: Roboto, Arial, sans-serif;
  height: 100%;
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: lightgray;
}

.animated-title>div {
  height: 50%;
  overflow: hidden;
  position: absolute;
  width: 100%;
}

.animated-title>div div {
  font-size: 3vw;
  /* position: absolute; */
}

.animated-title>div div span {
  display: block;
}

.animated-title>div.text-top {
  border-bottom: 5px solid #000;
  top: 0;
}

.animated-title>div.text-top div {
  -webkit-animation: showTopText 1s;
          animation: showTopText 1s;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  bottom: 0;
  transform: translate(0, 100%);
}


.animated-title>div.text-bottom {
  bottom: 0;
}

.animated-title>div.text-bottom div {
  -webkit-animation: showBottomText 0.5s;
          animation: showBottomText 0.5s;
  -webkit-animation-delay: 1.75s;
          animation-delay: 1.75s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  top: 0;
  transform: translate(0, -100%);
}

.text-top {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-bottom {
  display: flex;
  align-items: center;
  justify-content: center;
}

.bottom-content {
  width: 90%;
  height: 90%;
}

.aaron-suit {
  width: 90%;
  height: 90%;
}

.top-box {
  position: absolute;
}

